import {request} from './request'

export function getHomeMultiData() {
  return request({
    //url:'/test/getBanners'
	//url:'/vue/VueProjectData/home/product.php'
	url:'/vue/VueProjectData/manager/goods.php'
  });
}

export function getGoodsData(type, page) {
  return request({
	url:'/vue/VueProjectData/manager/goods.php',
    params:{
      type,
      page
    }
  });
}
//添加商品
export function seedAddGoodsData(obj,page=0) {
  return request({
	url:'/vue/VueProjectData/manager/addgoods.php',
    params:{
      obj,
      page
    }
  });
}
//上存图片
export function uploadImg(imgs) {
  return request({
	url:'/vue/VueProjectData/manager/uploadimg.php',
    params:{
      imgs
    }
  });
}
//修改商品
export function updateGoods(obj) {
  return request({
	url:'/vue/VueProjectData/manager/updategoods.php',
    params:{
      obj
    }
  });
}
//获取商品
export function getGoodsDatas(type='allSelect',page=0,productname,typeid=1) {
  return request({
	url:'/vue/VueProjectData/manager/product.php',
    params:{
		typeid,
		type,
		productname,
		page
    }
  });
}
//获取商品详情
export function getProductDetail(id=1) {
  return request({
	url:'/vue/VueProjectData/manager/productdetail.php',
    params:{
		id
    }
  });
}
