<template>
	<div class="product-detail-content">
		<div  class="p-info">{{title}}<span class="fright">打印</span></div>
		<!--成本分析-->
		<cost-analysis :prime-cost="primeCost"/>
		<!--库存信息-->
		<stock-info :stock="stock"/>
		<!--商品信息-->
		<product-info :pinfo="pinfo"/>
		<!--出入库明细-->
		<warehouse-detail :detail-info="storeAndshipment"/>
	</div>
</template>

<script>
import StockInfo from './StockInfo'
import ProductInfo from './ProductInfo'
import WarehouseDetail from './WarehouseDetail'
import CostAnalysis from './CostAnalysis'

export default {
  name: 'ProductDetailContent',
  components:{
	ProductInfo,
	StockInfo,
	WarehouseDetail,
	CostAnalysis,
  },
  data(){
	return{
		//成本分析
		primeCost:{
			oneMminPrice:0.00,
			thereMminPrice:0.00,
			newPrice:0.00,
			costs:0.00
		},
		//库存信息
		stock:{
			num:0,
			amount:0,
			warehouseName:'默认仓库',
			stockNum:0,
			stockMoney:0,
			sales:0,
			costs:0,
			price:0,
		},
		//货品信息
		pinfo:{
			encoded:'HPXXXXX',
			type:'',
			cid:0,
			manufacturer:'默认厂商',
			ctitle:'默认类别'
		},
		//出入库明细
		storeAndshipment:{
			stock:0,
		},
		title:'',
	}
  },
  methods:{

  },
  props:{
	DetailInfo:{
		type:Object,
		default(){
			return {}
		}
	}
  },
  //监听某个值
  watch:{
	DetailInfo(newVal){
		this.title=newVal.title;
		//成本分析
		this.primeCost.newPrice=newVal.price
		this.primeCost.costs=newVal.costs
		//库存
		this.stock.num=newVal.stock
		this.stock.warehouseName=newVal.warehouseid
		this.stock.sales=newVal.sales
		this.stock.costs=newVal.costs
		this.stock.price=newVal.price
		//货品信息
		this.pinfo.encoded=newVal.encoded
		this.pinfo.ctitle=newVal.ctitle
		this.pinfo.type=newVal.type
		this.pinfo.cid=newVal.cid
		//this.pinfo.manufacturer=newVal.manufacturer
		
		//出入库明细
		this.storeAndshipment.stock=newVal.stock
	}
  },
  activated(){
  }
}
</script>

<style>
  .p-info{
	background:#fff;
	line-height:40px;
	font-weight:600;
	padding:0 10px;
	color:black;
	font-size:15px;
	position:relative;
	height:40px;
  }
</style>
