<template>
	<swiper>
		<swiper-item v-for="(item,index) in banners" :key="index">
				<a href="#"><img :src="item.url" @load="swiperImgLoad"></a>
		</swiper-item>
	</swiper>
</template>

<script>
import {Swiper,SwiperItem} from 'components/common/swiper'

export default {
  name: 'ProductSwiper',
  props:{
	banners:{
		type:Array,
		default(){
			return []
		}
	}
  },
  data(){
	return{
		isLoad:false,
	}
  },
  components:{
	Swiper,
	SwiperItem
  },
  methods:{
	swiperImgLoad(){
		if(!this.isLoad){
			this.$emit("swiperImgLoad")
			this.isLoad=true
		}
	}
  }
}
</script>

