<template>
	<div class="product-detail">
		<!--顶部菜单-->
		<nav-bar class="product-detail-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				{{title}}
			</div>
			<div slot="right" class="slotright" >
				<i class="delete"  @click='deleteClick'><img src='~assets/images/common/delete.svg'></i>
				<i class="update" @click='updateClick'><img src='~assets/images/common/updated.svg'></i>
			</div>
		</nav-bar>
				<!--滚动--->
		<scroll class="product-detail-scroller"
				ref="scroller"
				:probe-type=3
		>
			<!--轮播图-->
			<product-swiper :banners="banners" @swiperImgLoad="swiperImgLoad"/>
			<product-detail-content :detail-info="detailInfoOne"/>
		</scroll>
	</div>
</template>

<script>


import NavBar from 'components/common/navbar/NavBar'

import ProductSwiper from './childComps/ProductSwiper'
import {getSwiperData} from 'network/category'
import {getProductDetail} from 'network/product'
import ProductDetailContent from './childComps/ProductDetailContent'
	

import Scroll from 'components/common/scroll/Scroll'


export default {
  name: 'ProductDetail',
  components:{
	NavBar,
	ProductSwiper,
	Scroll,
	ProductDetailContent
  },
  data(){
	return{
		title:'产品详情',
		banners:[],
		productDetailInfo:null,
		currentId:1,
	}
  },
  created(){
    //获取数据
	this.getProductDetail(this.currentId);
	//获取轮播图
	getSwiperData().then(res=>{
		this.banners.push(...res)
	})
  },
  methods:{
	swiperImgLoad(){
		//console.log("图片加载完成")
	},
	backClick(){
		this.$router.go(-1)
	},
	//获取商品详情信息
	getProductDetail(id){
		getProductDetail(id).then(res=>{
			this.productDetailInfo=res[0]
		})
	},
	updateClick(){
		//console.log('修改')
		this.$router.push({name:'updategoods',params:{baseInfo:this.productDetailInfo}}).catch(res=>{res})
	},
	//删除商品
	deleteClick(){
		console.log('删除商品')
	}
  },
  mounted(){
	this.$refs.scroller.refresh();		//刷新一下
	this.$refs.scroller.finishPullUp()	//获取最新高度
  },
  computed:{
	detailInfoOne(){
		//这里获取不了值
		return this.productDetailInfo
	}
  },
  activated(){
	this.currentId= this.$route.params && this.$route.params.id;
	this.getProductDetail(this.currentId)
  }
}
</script>

