.delete{
	position:absolute;
	top:8px;
	right:50px;
}
.update{
	top:8px;
	right:15px;
	position:absolute;
}
 .product-detail-nav-bar{
	background:#fff;
 }
 .product-detail-nav-bar .slotright{
	position:relative;
	text-align:right;
 }
 .pd10{
	padding:0 8px;
 }
 .product-detail{
	position:relative;
	height:100vh;
	background:#f1f1f1;
	z-index:13;
 }
 
 .product-detail-scroller{
    position:relative;
	height:calc(100% - 46px);
    right: 0;
    left: 0;
	overflow:hidden;
  }
  .button{
	text-align:center;
	margin:5px 0;
	padding:10px;
  }
  .button a{
	padding:4px 20px;
	border-radius:12px;
	border:1px solid black;
	font-size:12px;
	color:black;
	display:inline-block;
  }
    .icon-left{
	text-align:left;
	position:relative;
  }
  .icon-left img{
	position:absolute;
	top:10px;
	left:10px;
  }
</style>
