<template>
	<div class="stock-info">
		<h4 class="stock-title">库存信息<a href="#">查看库存详情</a></h4>
		<div class="stock-box">
			<ul class="info-item">
				<li class="blue">库存合计：<span class="bolds">{{stock.num}}</span></li>
				<li class="red">成本合计：<span class="bolds">{{allCotsMoneys}}</span></li>
			</ul>
			<ul class="info-item">
				<li>未发货：<span class="bolds black">{{stock.num}}</span></li>
				<li>销售价合计：<span class="bolds black">{{allPriceMoneys}}</span></li>
			</ul>
		</div>
		<div class="little-box">
			<div class="little-item">
				<h4>仓库名称</h4>
				<p>1号仓库</p>
			</div>
			<div class="little-item">
				<h4>库存</h4>
				<p class="blue">{{stock.num}}</p>
			</div>
			<div class="little-item">
				<h4>仓库金额</h4>
				<p class="red">{{allCotsMoneys}}</p>
			</div>
		</div>
	</div>
</template>

<script>

export default {
  name: 'StockInfo',
  components:{
  },
  methods:{
  },
  props:{
	stock:{
		type:Object,
		
		default(){
			return {}
		}
	}
  },
  computed:{
	allCotsMoneys(){
		return parseFloat(this.stock.costs*this.stock.num).toFixed(2)
	},
	allPriceMoneys(){
		return parseFloat(this.stock.price*this.stock.num).toFixed(2)
	}
  }
}
</script>

